// Card
// ---
$card-padding: 1.25em !default;
$card-background: $white !default;
$card-border: null !default;
$card-sep-border: $border !default;
$card-border-radius: $border-radius !default;
$card-box-shadow: $box-shadow-1dp !default;
$card-transition: all $transition-duration $transition-timing-function !default;

// Card elements
// ---
$card__title-font-size: 1.2em !default;
$card__title-font-weight: font-weight('semi-bold') !default;
$card__screen-opacity: 0.7 !default;
$card__screen-background: $dark !default;

// Card modifiers
// ---
$card-size-scale: (
  'lg': (
    'base': $card-padding,
    'sm': 2em
  ),
  'xl': (
    'base': $card-padding,
    'sm': 2em,
    'md': 4em 2.5em,
  )
) !default;
$card-tall-min-height: 18em !default;
$card-lift-shadow: $box-shadow-1dp !default;
$card-lift-shadow-hover: $box-shadow-8dp !default;
$card-step-offset: 5px !default;
$card-fade-transition: opacity $transition-duration $transition-timing-function !default;
$card-zoom-transition: transform 1s $transition-timing-function;
$card-link-extend: (
  'card_lift',
  'card_zoom'
) !default;

// Card themes
// ---
$card_theme_bordered-background: rgba($shade, 0) !default;
$card_theme_bordered-border: 2px solid $shade !default;
$card_theme_bordered-box-shadow: none !default;

$card_theme_bordered-background-hover: rgba($shade, 0.75) !default;
$card_theme_bordered-border-color-hover: darken($shade, 5%) !default;
$card_theme_bordered-box-shadow-hover: none !default;
