@import 'variables';

/**
 * Card theme dark
 * ---
 * Theme that adds the $dark variable as background and inverts content color.
 */

.card_theme_dark {
  background: $dark;
  color: color-switch($dark);

  @if (is-dark($dark)) {
    .card__header,
    .card__footer {
      border-color: $border-color-invert;
    }
  }

  .card__screen {
    background: $dark;
  }
}

