@import 'variables';

/**
 * Level spacing modifiers
 * ---
 * A modifier to change the spacing between level items.
 */

@each $key, $value in $level-spacing-scale {
  .level_spacing_#{$key} {
    > * + * {
      margin-left: $value;
    }

    &.level_wrap {
      margin: -($value * 0.5);

      > * {
        margin: ($value * 0.5);
      }
    }
  }
}
