@import 'variables';

/**
 * Button-group gap modifier
 * ---
 * Changes the gap (gutter) space between buttons.
 */

@each $key, $value in $button-group-gap-scale {
  .button-group_gap_#{$key} {
    > .button + .button {
      margin: 0 0 0 $value;
    }
  }

  .button-group_gap_#{$key}.button-group_stack {
    > .button + .button {
      margin: $value 0 0 0;
    }
  }

  .button-group_gap_#{$key}.button-group_wrap {
    margin: -($value * 0.5);

    > .button,
    > .button + .button {
      margin: ($value * 0.5);
    }
  }

}